import React from 'react';
import close from '../images/close.svg';

function PopupImage() {
  return (
    <div className="popup popup_picture">
      <div className="popup__picture">
        <img src={close} alt="картинка" className="popup__close popup__close_picture" />
        <img className="popup__image" alt="картинка" id="big_picture" />
      </div>
    </div>
  );
}

export default PopupImage;
