import React from 'react';
import close from '../images/close.svg';

function PopupWithForm(props) {
  return (
    <div className={`popup popup_${props.name}`}>
      <div className="popup__content">
        <img src={close} alt="картинка" className={`popup__close popup__close__${props.name}`}/>
        <h3 className="popup__title">{props.title}</h3>
        <form className="popup__form" name={props.name}>
{/*           <input type="text" name={props.name} className="popup__input popup__input_type_name" placeholder="Имя" />
          <span id="name-error" className="popup__error"></span>
          <input type="text" name="job" className="popup__input popup__input_type_link-url" placeholder="О себе" />
          <span id="job-error" className="popup__error"></span>
          <button className="button popup__button save__button" disabled>Сохранить</button> */}
          {props.children}
        </form>
      </div>
    </div>
  );
}

export default PopupWithForm;
